import "./App.css";
import { BrowserRouter as Router, Route, Switch } from "react-router-dom";
import { MatchPage } from "../src/pages/MatchPage";
import { TeamPage } from "../src/pages/TeamPage";
import SearchBar from "./components/SearchBar";
import HomePage from "./pages/HomePage";
import Error from "./pages/Error";
import NavBar from "./components/NavBar";
import TeamPerformPage from "./pages/TeamPerformPage";
import Footer from "./components/Footer";

function App() {
  return (
    <div className="App">
      <Router>
        <NavBar />
        <Switch>
          <Route exact path="/Ipl-Board">
            <HomePage />
          </Route>

          <Route exact path="/Ipl-Board/teams/:teamName">
            <SearchBar />
            <TeamPage />
          </Route>

          <Route exact path="/Ipl-Board/teams/:teamName/matches/:year">
            <SearchBar />
            <MatchPage />
          </Route>
          <Route exact path="/Ipl-Board/teams/performance/:teamName">
            <TeamPerformPage />
          </Route>

          <Route>
            <Error />
          </Route>
        </Switch>
      </Router>
      <Footer />
    </div>
  );
}

export default App;
